.Home {
    text-align: center;
  }

  .intro {
    padding:10px;
    background-color: rgba(0, 0, 0, 0.5);
    color:white;
    margin-top:-2em;
  }

  .Home-logo {
    padding:10px;
    background-color: rgba(0, 0, 0, 0.5);
    animation: Home-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
    border-radius: 40px;  
  }
    
  @keyframes Home-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  

  
  .home-background {   
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }