@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(255, 255, 255, 0.4)!important;
  background-blend-mode: lighten;
}

.background {
  background: url("https://expertwebinarseries.com/wp-content/uploads/2019/11/woman-in-restaurant-wearing-apron-1739748-1800x1200.jpg")no-repeat center center fixed;
    background-size: cover;
    min-height: 100vh;
    display: flex;

    background-color: rgba(255, 255, 255, 0.4)!important;
    background-blend-mode: lighten;

    flex-direction: column;
    align-items: center;
   
}

#nav_user {
  color:white;
}

.top-nav-collapse {
    background-color: #24355C;
  }

  .main-container{
   padding:2%;
  }
  .main-container  .card {
    min-height: 80vh;
    min-width: 90vw;
  }

  .white-txt {
    color:white;
    display: inline;
  }

  .card {
    margin-top: 5px;
    background: #eee;
    padding: 1em;
    line-height: 1.5em; 
    min-width:100%;
  }

  .card-close
  {
    cursor: pointer;
  }
  
  img.preview {
    max-width: 250px;
    max-height: 250px;
  }

  .table{
    table-layout:fixed;
    overflow: scroll;
    width: 100%;
  }

  .table th {
    font-size: .9rem;
    font-weight: 400;
}

  .table td {
    font-size: .9rem;
    font-weight: 300;
  }


/* change navbar background on collapse */
@media (max-width: 768px) {
  .white-txt{
    display: none;
  }
  .collapse{
    background: black;
    padding-left:15px;
    z-index: 999;
  }
  .card {
    margin-top: 20px;
    background: #eee;
    line-height: 1.5em; }

  .table th, .table .btn {
      font-size: .5rem;
  }
  
  .table td {
    font-size: .4rem;

  }
}



.form-wrapper {

  margin: 0 auto;
}
.App {
  text-align: center;
  margin-top: 30px;
}
nav {
  /** width: 300px; **/
  margin: 0 auto;
  background: #282c34;
  height: 70px;
  line-height: 70px;
}
nav .dropdown
{
  line-height: normal;
  border:3px solid red;
  margin:0px;
  display:block;
}

nav ul li {
  display: inline;
  list-style-type: none;
  text-align: center;
  padding: 30px;
}
nav ul li a {
  margin: 50px 0;
  font-weight: bold;
  color: white;
  text-decoration: none;
}
nav ul li a:hover {
  color: white;
  text-decoration: none;
}
