}
.App-link {
  color: #61dafb;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.um-main-body {
  height:100%;
}
.Home {
    text-align: center;
  }

  .intro {
    padding:10px;
    background-color: rgba(0, 0, 0, 0.5);
    color:white;
    margin-top:-2em;
  }

  .Home-logo {
    padding:10px;
    background-color: rgba(0, 0, 0, 0.5);
    -webkit-animation: Home-logo-spin infinite 20s linear;
            animation: Home-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
    border-radius: 40px;  
  }
    
  @-webkit-keyframes Home-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
    
  @keyframes Home-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  

  
  .home-background {   
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  img {
    max-width: 100%; }

   div.recipe {
